import React from "react"
import styled from "styled-components"

import PContainer from "@-taxi-parks-ui/container"

import PH1 from "./H/H1"
import H2 from "./H/H2"
import List from "./List"
import Steps, { Step } from "./Steps"

const H1 = styled(PH1)`
  ${tw`text-center`}
`

const P = styled.p`
  ${tw`mb-2`}
`

const Container = styled(PContainer)`
  ${tw`bg-grey-lighter`}
`

const WorkStart = () => (
  <Container>
    <H1>{"  ?"}</H1>
    <Steps>
      <Step
        content={<P>{"    "}</P>}
        title={<H2>{"  "}</H2>}
      />
      <Step
        content={
          <>
            <P>{"   :"}</P>
            <List>
              <li>{" "}</li>
              <li>{" "}</li>
              <li>
                {
                  "    (    )"
                }
              </li>
            </List>
          </>
        }
        title={<H2>{" "}</H2>}
      />
      <Step
        content={
          <P>
            {"      "}
          </P>
        }
        title={<H2>{" "}</H2>}
      />
      <Step
        content={
          <P>{"       "}</P>
        }
        title={<H2>{""}</H2>}
      />
    </Steps>
  </Container>
)

export default WorkStart
